.item {
	width: 100%;
	transition: all .5s linear;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.item p {
	border-bottom: 2px whitesmoke solid;
	background: #fff;
	width: 100%;
	height: 100%;
	padding: 10px;
	color: #333;
	cursor: pointer;
}


.item button {
	border: none;
	background: tomato;
	font-weight: 700;
	color: #fff;
	margin: 0 10px;
	cursor: pointer;
	padding: 5px 10px;
	border-radius: 5px;
	transition: all .5s linear;
	text-transform: uppercase;
}
.item button:hover{
	background: #de5f44;
}
