.app {
	padding: 80px 0;
}

.title {
	text-align: center;
}

.small {
	color: #6C3483;
}

.todo_list {
	padding-top: 60px;
}

.container {
	width: 100%;
	margin: 0 auto;
}

.buttons {
	margin: 0 auto;
	margin-top: 40px;
	display: flex;
	align-items: center;
	justify-content: space-around;
	max-width: 460px;
	height: 100%;
}

.buttons button {
	text-transform: uppercase;
	font-weight: 700;
	font-size: 16px;
	padding: 10px;
	border: none;
	border-bottom:2px #333 solid;
	color: #333;
	transition: all .5s linear;
	cursor: pointer;
}
.buttons button:hover{
	color: #5B2C6F;
	background: #fcf4ff;
	border-radius: 5px 5px 0 0;
	border-bottom: 2px #5B2C6F solid;
}


.todos {
	margin: 0 auto;
	margin-top: 40px;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	max-width: 460px;
	height: 100%;
	background: #f5f5f5;
	padding: 5px;
	border-radius: 5px;
}

.todos p {
	align-self: start;
	color: #b9b9b9;
	padding: 5px;
}

.todos small {
	padding: 0 5px;
	color: #333;
	font-size: 16px;
	font-weight: bold;
	animation: blinking 3s linear infinite;
}

.list {
	width: 100%;
}

.bottomControl{
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.bottomControl button{
	padding: 9px 20px;
	border-radius: 5px;
	background: tomato;
	border: none;
	color: #fff;
	text-transform: uppercase;
	font-weight: 700;
	font-size: 16px;
	transition: all .5s ease;
	margin-bottom: 15px;
}

.bottomControl button:hover{
	background: #de5f44;
}

.separator{
	background: #5B2C6F;
	width: 70%;
	height: 1px;
	margin: 10px auto;
}

.bottomControl .toggle{
	background: #5B2C6F;
}

.bottomControl .toggle:hover{
	background: #8E44AD;
}

@keyframes blinking {
	0% {
		color: #333;
	}
	50% {
		color: tomato;
	}
	100% {
		color: #333;
	}
}